.banner {
  height: 600px;
  width: inherit;
  //background-image: url('../images/articles/banner1.png') // should be set by blade
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  //flex-direction: column
  margin-bottom: 20px;
  > div {
    background-color: rgba(black, 0.7);
    min-height: 100px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    padding: 0 20px;
    p.category {
      background-color: $light-blue;
      padding: 5px 10px;
      color: white;
      text-transform: uppercase;
      font-size: 18px; }
    div.profile {
      height: 40px;
      width: 40px;
      display: inline-block;
      background-image: url('../images/chris.jpg');
      background-size: cover;
      border-radius: 100px;
      margin-right: 10px; }
    p {
      color: white;
      margin: 0;
      text-transform: capitalize;
      font-size: 22px;
      font-family: RalewayLight;
      display: inline-block;
      position: relative;
      bottom: 10px;
      span {
        color: #7ac1f4;
        &:hover {
          color: #6aa4cd;
          cursor: pointer; } }
      span.timestamp {
        color: white;
        text-transform: lowercase; }
      &.title {
        position: inherit;
        outline: none;
        font-size: 30px;
        font-family: RalewayRegular; } } } }
